@import url(https://fonts.google.com/specimen/Pangolin?selection.family=Indie+Flower|Neucha|Pangolin);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Pangolin', 'Neucha', 'Open Sans', 'Helvetica Neue', sans-serif;
  src: url('https://fonts.google.com/specimen/Pangolin?selection.family=Indie+Flower|Neucha|Pangolin')
}

.App {
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content
}




